@import 'scss/base';

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $color-black;
  cursor: pointer;
  padding: 12px 20px;

  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;

  @media (min-width: $breakpoint-tablet) {
    padding: 28px 30px;
    position: relative;
  }

  @media (min-width: $breakpoint-PC) {
    padding: 24px 91px 21px 81px;
  }

  &__logo-icon {
    width: 71px;
    height: 24px;

    @media (min-width: $breakpoint-PC) {
      width: 104px;
      height: 35px;
    }
  }

  &__menu {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin-top: 48px;
    width: 100%;
    list-style-type: none;

    &-item {
      display: flex;
      justify-content: center;
      margin: 0;
      width: 100%;
      background-color: rgba($color-black, 0.7);

      &__name {
        overflow: hidden;

        &:hover {
          background: linear-gradient(100deg, $color-chartreuse-yellow, $color-hot-pink);
          color: transparent;
          -webkit-background-clip: text;
        }

        @media (min-width: $breakpoint-tablet) {
          margin-right: 30px;
        }

        @media (min-width: $breakpoint-PC) {
          margin-right: 55px;
        }
      }
    }

    @media (min-width: $breakpoint-tablet) {
      margin: 0;
      flex-direction: row;
      position: relative;
      justify-content: flex-end;
      width: auto;
    }
  }

  &__menu-container {
    display: flex;
    height: 100%;
    width: 30px;
    cursor: pointer;

    &__button,
    &__button::before,
    &__button::after {
      display: block;
      background-color: $color-white;
      position: absolute;
      height: 1.5px;
      width: 22px;
      transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
      border-radius: 2px;
    }

    &__button {
      &::before {
        content: '';
        margin-top: -8px;
      }

      &::after {
        content: '';
        margin-top: 8px;
      }
    }

    @media (min-width: $breakpoint-tablet) {
      display: none;
    }
  }

  #menu-toggle {
    display: none;

    &:checked + .header__menu-container .header__menu-container__button:before {
      margin-top: 0;
      transform: rotate(405deg);
    }

    &:checked + .header__menu-container .header__menu-container__button {
      background: rgba(255, 255, 255, 0);
    }

    &:checked + .header__menu-container .header__menu-container__button:after {
      margin-top: 0;
      transform: rotate(-405deg);
    }

    &:checked ~ .header__menu .header__menu-item {
      border-bottom: 1px solid $color-black;
      height: 50px;
      transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);

      .header__menu-item__name {
        margin: auto;
      }
    }

    & ~ .header__menu .header__menu-item {
      height: 0;
      margin: 0;
      padding: 0;
      border: 0;
      transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);

      @media (min-width: $breakpoint-tablet) {
        height: auto;
      }
    }
  }
}

.banner {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 0 34px 44px;
  position: relative;
  overflow: hidden;

  background-image: linear-gradient(to left, rgba($color-white, 0), rgba($color-black, 0.7)),
    url('/assets/background.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (min-width: $breakpoint-tablet) {
    justify-content: inherit;
    flex-direction: row;
    padding: 100px 30px;
  }

  @media (min-width: $breakpoint-PC) {
    padding: 60px 80px;
    position: relative;
    justify-content: space-evenly;
  }

  &__content {
    text-align: center;

    &-title {
      font-size: 36px;

      span {
        color: $color-hot-pink;
      }

      @media (min-width: $breakpoint-tablet) {
        font-size: 48px;
      }

      @media (min-width: $breakpoint-PC) {
        font-size: 72px;
      }
    }

    &-subtitle {
      font-size: 16px;
      padding: 17px 0;

      span {
        color: $color-chartreuse-yellow;
      }

      @media (min-width: $breakpoint-tablet) {
        font-size: 22px;
        padding: 22px 0;
      }

      @media (min-width: $breakpoint-PC) {
        font-size: 32px;
      }
    }

    &-button {
      background-color: $color-chartreuse-yellow;
      color: $color-black;
      border: none;
      padding: 7px 40px;
      font-size: 16px;
      border-radius: 100px;
      font-family: $font-primary;
      cursor: pointer;

      &:hover {
        background-color: rgba($color-chartreuse-yellow, 0.6);
      }

      @media (min-width: $breakpoint-tablet) {
        padding: 14px 40px;
        font-size: 22px;
      }
    }

    @media (min-width: $breakpoint-tablet) {
      width: 65%;
      text-align: left;
    }
  }

  &__icon {
    margin-top: -70px;

    @media (min-width: $breakpoint-tablet) {
      margin-top: 0;
      position: absolute;
      right: -90px;
    }

    @media (min-width: $breakpoint-PC) {
      position: initial;
    }
  }
}

.offers {
  background-color: $color-san-juan;
  padding: 50px 21px;
  font-weight: $font-weight-extrabold;

  &__title {
    font-size: 18px;
    padding-bottom: 30px;

    @media (min-width: $breakpoint-tablet) {
      font-size: 24px;
    }

    @media (min-width: $breakpoint-PC) {
      font-size: 32px;
      padding-bottom: 40px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 30px;

    &-item {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 100%;
      background-position: center;
      padding: 90px 25px 35px 25px;
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 10px;

      @media (min-width: $breakpoint-tablet) {
        width: 41%;
        padding: 90px 20px 28px 25px;
      }

      @media (min-width: $breakpoint-PC) {
        width: 43%;
        padding: 35px 30px 35px 25px;

        &:first-child,
        &:last-child {
          width: 100%;

          & .offers__list-item__title {
            font-size: 74px;
            line-height: 74px;
          }

          & .offers__list-item__subtitle {
            font-size: 26px;
            line-height: 31px;
          }
        }
      }

      &__title {
        font-size: 40px;
        width: 80%;
        line-height: 40px;

        @media (min-width: $breakpoint-PC) {
          font-size: 46px;
          line-height: 46px;
          width: 60%;
        }
      }

      &__subtitle {
        font-size: 16px;
        padding: 22px 0;
        line-height: 19px;

        @media (min-width: $breakpoint-PC) {
          font-size: 24px;
          line-height: 28px;
        }
      }

      &__button {
        background-color: transparent;
        color: $color-chartreuse-yellow;
        border-radius: 100px;
        border: 2px solid $color-chartreuse-yellow;
        padding: 7px 40px;
        font-size: 16px;
        width: fit-content;

        &:hover {
          color: rgba($color-chartreuse-yellow, 0.6);
          border: 2px solid rgba($color-chartreuse-yellow, 0.6);
          cursor: pointer;
        }

        @media (min-width: $breakpoint-PC) {
          padding: 14px 40px;
          font-size: 22px;
        }
      }

      &:first-child {
        background-image: linear-gradient(to left, rgba($color-white, 0), rgba($color-black, 0.7)),
          url('/assets/bg-first.png');
      }

      &:nth-child(2) {
        background-image: linear-gradient(to left, rgba($color-white, 0), rgba($color-black, 0.7)),
          url('/assets/bg-2.png');
      }

      &:nth-child(3) {
        background-image: linear-gradient(to left, rgba($color-white, 0), rgba($color-black, 0.7)),
          url('/assets/bg-3.png');
      }

      &:last-child {
        background-image: linear-gradient(to left, rgba($color-white, 0), rgba($color-black, 0.7)),
          url('/assets/bg-last.png');
      }
    }
  }

  &__journey {
    margin-top: 50px;

    @media (min-width: $breakpoint-tablet) {
      margin-top: 55px;
    }

    @media (min-width: $breakpoint-PC) {
      margin-top: 60px;
    }

    &__title {
      color: $color-white;
      font-size: 18px;
      line-height: 21px;
      font-weight: $font-weight-extrabold;

      @media (min-width: $breakpoint-tablet) {
        font-size: 24px;
        line-height: 28px;
      }

      @media (min-width: $breakpoint-PC) {
        font-size: 32px;
        line-height: 38px;
      }
    }

    &__subtitle {
      font-size: 14px;
      line-height: 16px;
      color: rgba($color-white, 0.6);
      margin: 17px 0;
      font-weight: $font-weight-bold;

      @media (min-width: $breakpoint-tablet) {
        font-size: 16px;
        line-height: 19px;
      }

      @media (min-width: $breakpoint-PC) {
        font-size: 20px;
        line-height: 24px;
      }
    }

    &__button {
      color: $color-chartreuse-yellow;
      background-color: transparent;
      font-size: 14px;
      line-height: 16px;
      font-weight: $font-weight-extrabold;
      border: none;
      padding: 0;

      &:hover {
        color: rgba($color-chartreuse-yellow, 0.6);
        cursor: pointer;
      }

      &:active {
        border: none;
      }

      @media (min-width: $breakpoint-tablet) {
        font-size: 16px;
        line-height: 19px;
      }

      @media (min-width: $breakpoint-PC) {
        font-size: 20px;
        line-height: 24px;
      }
    }
  }

  @media (min-width: $breakpoint-tablet) {
    padding: 53px 30px;
  }

  @media (min-width: $breakpoint-PC) {
    padding: 60px 80px;
  }
}

.footer {
  background-color: $color-black;
  text-align: center;

  &__subtitle {
    color: $color-chartreuse-yellow;
    font-weight: $font-weight-extrabold;
    font-size: 18px;
    padding-bottom: 20px;
  }
}
